/* Í∏∞Ï°¥ CSS Î≥ÄÏàòÎì§ Í∞úÏÑ† */
:root {
    --primary: #6a47b8;
    --primary-hover: #5339a0;
    --success: #43a047;
    --warning: #fb8c00;
    --bg: #f8f9fa;
    --element: #ffffff;
    --element-light: #f3f4f6;
    --border: #e5e7eb;
    --border-light: #f3f4f6;
    --text: #111827;
    --text-light: #6b7280;
    --text-medium: #4b5563;
    --shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    --radius: 6px;
    --transition: none;
    --header-height: 64px;
    --footer-height: 60px;
}

/* Îã§ÌÅ¨ Î™®Îìú Î≥ÄÏàòÎì§ */
body.dark-mode {
    --primary: #7e57c2;
    --primary-hover: #5e35b1;
    --success: #4caf50;
    --warning: #ff9800;
    --bg: #1e1e1e;
    --element: #252525;
    --element-light: #2d2d2d;
    --border: #444444;
    --border-light: #333333;
    --text: #e0e0e0;
    --text-light: #aaaaaa;
    --text-medium: #cccccc;
    background-color: var(--bg);
}

/* Í∏∞Ï°¥ Í∏∞Î≥∏ Ïä§ÌÉÄÏùºÎì§ Ïú†ÏßÄ */
*, *::before, *::after {
    transition: var(--transition);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Segoe UI', 'Roboto', 'Helvetica Neue', sans-serif;
    background-color: var(--bg);
    margin: 0;
    padding: 0;
    color: var(--text);
    min-height: 100vh;
}

/* ============================================
   üÜï ModernSidebar Ï†ÑÏö© Ïä§ÌÉÄÏùº
   ============================================ */

.sidebar {
    width: 280px;
    border-right: 1px solid var(--border);
    display: flex;
    flex-direction: column;
    transition: width 0.3s ease, transform 0.3s ease;
    background-color: var(--element);
    color: var(--text);
    height: 100vh;
    z-index: 10;
    position: relative;
    flex-shrink: 0;
}

.sidebar.collapsed {
    width: 0;
    transform: translateX(-100%);
    overflow: hidden;
    margin-left: -1px;
    padding: 0;
    border: none;
}

.modern-sidebar {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background-color: var(--element);
    border-right: 1px solid var(--border);
    overflow: hidden;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î Ìó§Îçî - Ï†ïÎ†¨ ÏàòÏ†ï */
.modern-sidebar-header {
    padding: 12px 16px;
    border-bottom: 1px solid var(--border-light);
    background-color: var(--element);
    flex-shrink: 0;
}

.sidebar-title-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 28px;
    min-height: 28px;
}

.sidebar-title {
    font-size: 13px !important;
    font-weight: 600 !important;
    color: var(--text);
    margin: 0;
    line-height: 1.4 !important;
    display: flex;
    align-items: center;
    height: 20px;
}

.new-file-button {
    width: 28px;
    height: 28px;
    border: none;
    background: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: var(--radius);
    transition: var(--transition);
    color: var(--text-light);
    flex-shrink: 0;
}

.new-file-button:hover {
    background-color: var(--element-light);
    color: var(--text);
}

body.dark-mode .new-file-button:hover {
    background-color: rgba(255, 255, 255, 0.05);
}

.icon-plus {
    font-size: 16px;
    font-weight: 600;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î ÏΩòÌÖêÏ∏† */
.modern-sidebar-content {
    flex: 1;
    overflow-y: auto;
    padding: 8px 12px;
    background-color: var(--element);
}

/* ÏÑπÏÖò Ïä§ÌÉÄÏùº - Í∞ÑÍ≤© Ï§ÑÏù¥Í≥† Ïª¥Ìå©Ìä∏ÌïòÍ≤å */
.sidebar-section {
    margin-bottom: 4px; /* Í∞ÑÍ≤© Ï§ÑÏûÑ */
}

/* ÏÑπÏÖò Ï†úÎ™©ÏùÑ Ìó§Îçî ÌÖçÏä§Ìä∏ÏôÄ Í∞ôÏùÄ ÌÅ¨Í∏∞Î°ú ÎßûÏ∂§ */
.section-title {
