/* ============================================
   üÜï ModernSidebar Ïï†ÎãàÎ©îÏù¥ÏÖò Î∞è Ï∂îÍ∞Ä Ïä§ÌÉÄÏùº
   ============================================ */

/* Ïï†ÎãàÎ©îÏù¥ÏÖò Ìö®Í≥º */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.section-content {
    animation: fadeInUp 0.3s ease-out;
}

.file-item {
    animation: fadeInUp 0.2s ease-out;
}

.file-item:nth-child(1) { animation-delay: 0.05s; }
.file-item:nth-child(2) { animation-delay: 0.1s; }
.file-item:nth-child(3) { animation-delay: 0.15s; }
.file-item:nth-child(4) { animation-delay: 0.2s; }
.file-item:nth-child(5) { animation-delay: 0.25s; }

/* Ìò∏Î≤Ñ Ìö®Í≥º */
.file-item:hover .file-icon {
    transform: scale(1.1);
}

.section-header:hover .chevron-icon {
    transform: scale(1.1);
}

/* Ìè¨Ïª§Ïä§ Ï†ëÍ∑ºÏÑ± */
.section-header:focus,
.file-item:focus,
.new-file-button:focus,
.auth-button:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
}

/* Î°úÎî© ÏÉÅÌÉú */
.file-item.loading {
    opacity: 0.6;
    pointer-events: none;
    position: relative;
}

.file-item.loading::after {
    content: "";
    position: absolute;
    right: 8px;
    top: 50%;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    border: 2px solid var(--border);
    border-top: 2px solid var(--primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: translateY(-50%) rotate(0deg); }
    100% { transform: translateY(-50%) rotate(360deg); }
}

/* ÌäπÎ≥ÑÌïú ÏÉÅÌÉú ÌëúÏãú */
.file-item.modified {
    position: relative;
}

.file-item.modified::before {
    content: "‚óè";
    position: absolute;
    left: -8px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--warning);
    font-size: 12px;
}

.file-item.new {
    background-color: rgba(67, 160, 71, 0.1);
    border-left: 2px solid var(--success);
}

body.dark-mode .file-item.new {
    background-color: rgba(67, 160, 71, 0.15);
}

/* ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ìö®Í≥º */
